@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url(./reset.css);

html,
body {
  height: 100%;
}
body {
  /* background-image: url(./img/00bg.png); */
  background-repeat: no-repeat;
  background-position: center 0;

  font-family: "Noto Sans KR", sans-serif;
  font-size: 16px;
  color: #3a3a4a;
  line-height: 1.4;
}

/* header *****************************************************************/

#wrap {
}

@media (min-width: 1600px) {
  #wrap:before,
  #wrap:after {
    width: 170px;
    content: "";
    position: absolute;
    z-index: 1;
  }
  #wrap:before {
    height: 400px;
    top: 0;
    left: 0;
    background: url("./img/bg_layout_l.png") no-repeat;
  }

  #wrap:after {
    height: 390px;
    top: 47px;
    right: 0;
    background: url("./img/bg_layout_r.png") no-repeat;
  }

  /* #wrap.no-bg:before,#wrap.no-bg:after {
        display: none
    } */
}

h2 {
  font-size: 21px;
  font-weight: 600;
}
h3 {
  font-size: 60px;
  font-weight: 600;
}

header {
  width: 1180px;
  height: auto;
  margin: auto;
}
header .header__top {
  width: 100%;
  height: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header__top .header__zoom {
  display: flex;
  align-items: center;
}
header .header__top .header__zoom p {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
header .header__top .header__zoom p em {
  font-size: 13px;
  margin-right: 4px;
}
header .header__top .header__zoom p a {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  background-color: #f1f1f1;
  font-size: 14px;
  text-align: center;
  line-height: 1.5;
  margin: 0 2px;
}

header .header__top .header__usermenu {
}
header .header__top .header__usermenu ul {
  display: flex;
  align-items: center;
}
header .header__top .header__usermenu ul li {
  font-size: 13px;
  position: relative;
}
header .header__top .header__usermenu ul li::after {
  content: "ã…£";
  color: #e1e1e1;
}
header .header__top .header__usermenu ul li:last-child::after {
  content: "";
}
header .header__top .header__usermenu ul li:last-child {
  margin: 0 0 0 8px;
}
header .header__top .header__usermenu ul li a {
  display: inline-block;
  padding: 0 10px;
}
header .header__top .header__usermenu ul li button {
  background: #fff url(./img/arrow-navy-8x6.png) no-repeat top 7px right 0;
  border: 0;
  outline: 0;
  margin: 0 8px;
  padding-right: 16px;
  cursor: pointer;
}
header .header__top .header__usermenu ul li button:hover li {
  border: 10px solid #121dfa;
}
header .header__top .header__usermenu ul li:last-child button {
  margin: 0;
  color: #d7021b;
}

header .header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}
header .header__nav h1 {
}
header .header__nav h1 img {
}
header .header__nav nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header__nav nav ul {
  display: flex;
  align-items: center;
}
header .header__nav nav ul li {
  font-size: 20px;
  font-weight: 800;
  padding: 0 30px;
}
header .header__nav nav p {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header__nav nav p input[type="text"] {
  border: 1px solid #ffffff;
  border-radius: 4px;
  padding: 0 15px;
  height: 32px;
  width: 30px;
  background: url(./img/ico-search-navy.png) no-repeat top 4px right 0;
  cursor: pointer;
}
header .header__nav nav p #nav__button {
  background: url(./img/ico-menu-navy.png) no-repeat top 7px right 0;
  width: 32px;
  height: 32px;
  border: 0;
  outline: 0;
  cursor: pointer;
  margin-left: 8px;
}
header .header__nav nav p input[type="text"]:focus {
  border: 1px solid #eee;
  padding: 0 15px;
  height: 32px;
  width: 180px;
  outline: 0;
  background-position: top 4px right 6px;
}
header .header__nav nav p span {
  font-size: 28px;
}

