/* nav-popup *****************************************************************/
#nav-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1000;
  display: none;
}
#nav-popup:target {
  display: block;
}
#nav-popup .all-menu-wrap {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
  width: 90%;
  height: 90%;
  min-width: 1180px;
  background-color: #fff;
  box-shadow: 0 0 3px #000;
  border-radius: 4px;
}

#nav-popup .all-menu-wrap .all-top h4 {
  padding: 0 30px;
  color: #fff;
  font-size: 21px;
  font-weight: 700;
  background-color: #44465c;
  line-height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#nav-popup .all-menu-wrap .all-top h4 a {
  color: #fff;
  font-size: 32px;
}

#nav-popup .all-menu-wrap .all-top ul {
  display: flex;
  justify-content: end;
  align-items: center;
  padding: 0 60px 0 0;
  height: 48px;
  border-bottom: 1px solid #e1e3e6;
}
#nav-popup .all-menu-wrap .all-top ul li {
  font-size: 15px;
  position: relative;
}
#nav-popup .all-menu-wrap .all-top ul li::after {
  content: "ã…£";
  color: #e1e1e1;
}
#nav-popup .all-menu-wrap .all-top ul li:last-child::after {
  content: "";
}
#nav-popup .all-menu-wrap .all-top ul li:last-child {
  margin: 0 0 0 8px;
}
#nav-popup .all-menu-wrap .all-top ul li a {
  display: inline-block;
  padding: 0 10px;
}
#nav-popup .all-menu-wrap .all-top ul li:last-child a {
  padding: 0 0 0 10px;
}

#nav-popup .all-menu-wrap .all-menu {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: calc(100% - 120px);
  align-items: stretch;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left {
  width: 220px;
  padding: 30px 0 0;
  background-color: #f6f6f6;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left ul {
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left ul li a {
  height: 42px;
  padding: 0 42px;
  display: flex;
  align-items: center;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left ul li a:hover {
  color: #fff;
  background-color: #ee0700;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left ul li.curr a {
  color: #fff;
  background-color: #ee0700;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-left ul li a {
}

#nav-popup .all-menu-wrap .all-menu .all-menu-right {
  width: 100%;
  padding: 30px 60px;
  overflow-y: scroll;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right p {
  display: block;
  margin: 0 0 15px;
  color: #3a3a4a;
  font-size: 16px;
  font-weight: 700;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right p a {
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right > ul {
  border-top: 2px solid #44465c;
  border-bottom: 1px solid #bec2c8;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li {
  padding: 18px 10px 18px 0;
  color: #44465c;
  font-size: 15px;
  border-bottom: 1px solid #e1e3e6;
  display: flex;
  justify-content: flex-start;
  align-items: start;
  width: 100%;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li a {
  width: 240px;
  flex-shrink: 0;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li ul {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li ul li {
  border: 0;
  padding: 0;
  height: 26px;
  width: 50%;
  display: revert;
  position: relative;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li ul li::before {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: -10px;
  width: 3px;
  height: 3px;
  background-color: #44465c;
  border-radius: 50%;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li ul li a {
  width: 100%;
  color: #6c6d73;
}
#nav-popup .all-menu-wrap .all-menu .all-menu-right ul li ul li a:hover {
  text-decoration: underline;
  color: #ee0700;
}
#nav-popup .all-menu-wrap-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7);
}
